import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components: {
		uniLoadMore	
	},
	data() {
		return {
			specClass: 'none',
			addClass:'none',
			status:null,//10:上架,20:下架
			goodsId:'',//商品id,示例 24,25,26代表三个商品
			goodsList:[],//订单列表
			categoryList:[],//商品分类列表
			loadingType: 'more', //more 是加载中   nomore是加载结束
			total:0,//总条数
			pageSize:0,//每页条数
			page:1,//当前页
			allChecked:0,
			//订单列表
			tabList: [{
					state: 0,
					text: '下架',
				},
				{
					state: 1,
					text: '分类至',
				}
			],
			
			nextPage:false,//是否有下一页
			token:'',
			cateId:'',//分类id
			classification:'',//分类名称
		};
	},
	
	async onLoad(){
		
		let userInfo=uni.getStorageSync('userInfo');
		
		if(userInfo!==undefined){
			this.token = userInfo.token;
			this.loadData();
		}else{
			uni.navigateTo({
				url:'/pages/login/login'
			})
		}
		
		
	},
	onShow() { //返回显示页面状态函数
		//，来进行局部刷新
		this.loadData();
	},
	
	methods: {
		//获取上架列表数据
		async loadData(){
			let self = this;
			uni.request({
				url: 'https://wxshop.qclike.cn/api/admins/goodsList',
				header:{
					'content-type': 'application/x-www-form-urlencoded', 
				},
				method:'POST',
				data:{
					token:self.token,
					page:self.page,
					sort_type:'asc',//升序
					sort_name:'createtime',//创建时间
					goods_status:10,//上架
				},
				success: function(res) {
					if(res.data.code == 1){
						let result = res.data.data;
						if(res.data.code == 1){
							
							if(self.page == 1){//第一次加载
								self.goodsList = result.data;
								self.goodsList = self.goodsList.map(item=>{
									item.checked = false;
									return item;
								});
							}else{//加载更多
								result.data = result.data.map(item=>{
									item.checked = false;
									return item;
								});
								result.data.forEach((item)=>{
									self.goodsList.push(item) ;
								})
							}
							self.pageSize = result.per_page;//每页条数
							self.total = result.total;//总条数
							self.page = result.current_page; //当前页
							
							//判读是否有下一页数据
							if(self.total > self.pageSize * self.page){
								self.loadingType = 'more';
								self.nextPage = true;
							}else{
								self.loadingType = 'nomore';
								self.nextPage = false;
							}
						}
					}
					console.log(res.data);
				}
			});
			if(self.loadingType === 'loading'){
				//防止重复加载
				return;
			}
			self.loadingType = 'loading';
		}, 
		//禁止手指滑动
		stopTouchMove: function() {
		  return false;
		},

		
		
		//触发加载更多
		loadMore(){
			let self = this;
			if(self.nextPage){
				self.page = self.page +1;
				//调接口更新数据
				self.loadData();
			}
		},
		//底部tab切换
		tabClick(index){
			
			let self = this;
			let goodsIdArr= [];
			
			self.goodsList.forEach((item,index)=>{
				if(item.checked){
					goodsIdArr.push(item.goods_id);
				}
				
			})
			goodsIdArr.forEach((item,index)=>{
				if(goodsIdArr.length-1 > index){
					self.goodsId += item+','
				}else{
					self.goodsId += item;
				}
				
			})
			if(goodsIdArr.length == 0){
				uni.showToast({title:'请先选择商品',icon:'none'});
			}else{
				//调接口处理
				if(index == 0){ //批量下架
					 uni.request({
						url: 'https://wxshop.qclike.cn/api/admins/upordowngoods',
						header:{
							'content-type': 'application/x-www-form-urlencoded', 
						},
						method:'POST',
						data:{
								token:self.token,
								status:20,
								goods_id:self.goodsId
							},
							success: function(res) {
								if(res.data.code == 1){
									 self.loadData();
								} else if(res.data.code == 0){
									let msg = res.data.msg
									uni.showToast({title: msg,icon:"none"});
								}
						}
					});						
					
				}else if(index == 1){
					
					if(self.specClass === 'show'){
						self.specClass = 'hide';
						setTimeout(() => {
							self.specClass = 'none';
						}, 250);
					}else if(self.specClass === 'none'){
						self.specClass = 'show';
					}
					uni.request({
						url: 'https://wxshop.qclike.cn/api/admins/showlist', 
						method:'POST',
						data:{
							token:self.token
						},
						success: function(res) {
							if(res.data.code == 1){
								self.categoryList =  res.data.data.categorydata;
							} else if(res.data.code == 0){
								
							}
							console.log(res.data);
						}
					});
					
					//改分类
					
				}
				
			 
			 
			 
			}
		},
		//选择批量操作
		radioSelect(index){
			let self = this;
			self.goodsList[index].checked = !self.goodsList[index].checked;
			//进行强制渲染
			self.$forceUpdate();
			
		},
		//弹窗开关
		toggleSpec() {
			if(this.specClass === 'show'){
				this.specClass = 'hide';
				setTimeout(() => {
					this.specClass = 'none';
				}, 250);
			}else if(this.specClass === 'none'){
				this.specClass = 'show';
			}
		},
		//确认按钮
		toggleSpecTab(todo){
			let self = this;
			if(self.specClass === 'show'){
				self.specClass = 'hide';
				setTimeout(() => {
					self.specClass = 'none';
				}, 250);
			}else if(self.specClass === 'none'){
				self.specClass = 'show';
			}
			
			 uni.request({
				url: 'https://wxshop.qclike.cn/api/admins/goodscate',
				method:'POST',
				header:{
					'content-type': 'application/x-www-form-urlencoded', 
				},
				data:{
						token:self.token,
						goods_id:self.goodsId,
						cate_id:todo.id
						
					},
					success: function(res) {
						if(res.data.code == 1){
							self.loadData();
						} else if(res.data.code == 0){
							let msg = res.data.msg
							uni.showToast({title: msg,icon:"none"});
						}
				}
			});						
			
		},
		
		//新增分类
		addClassification(){
			this.addClass = 'show';
			this.specClass = 'none';
		},
		//确认新增分类
		tabConfirm(){
			let self = this;
			if(!self.classification){ 
				uni.showToast({title: '请输入分类名称',icon:"none"});
				return ; 
			}
			self.specClass= 'show';
			self.addClass = 'none';
			uni.request({
				url: 'https://wxshop.qclike.cn/api/admins/cateupdate',
				method:'POST',
				header:{
					'content-type': 'application/x-www-form-urlencoded', 
				},
				data:{
						token:self.token,
						parent_id:0,
						cate_name:self.classification
						
					},
					success: function(res) {
						if(res.data.code == 1){
							
							self.classification = '';
							self.specClass = 'none';
							self.$nextTick(function(){
								self.tabClick(1);
							})
							
						} else if(res.data.code == 0){
							let msg = res.data.msg
							uni.showToast({title: msg,icon:"none"});
						}
				}
			});		
		},
		
		//取消新增分类
		tabCancel(){
			console.log('取消新增分类')
			this.specClass= 'show';
			
			this.addClass = 'none';
		},
		toggleAddClass(){
			if(this.addClass === 'show'){
				this.addClass = 'hide';
				setTimeout(() => {
					this.addClass = 'none';
				}, 250);
			}else if(this.addClass === 'none'){
				this.addClass = 'show';
			}
		}
		
		
	}
}
